.button {
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
}

.button-green {
    background-color: white; 
    color: black; 
    border: 2px solid #4CAF50;
}
  
.button-green:hover {
    background-color: #4CAF50;
    color: white;
}

.button-red {
    background-color: white; 
    color: black; 
    border: 2px solid #f44336;
  }
  
.button-red:hover {
background-color: #f44336;
color: white;
}
  